@value todoBlue from '../../common/colors.css';
@value todoWhite from '../../common/colors.css';
@value todoBlack from '../../common/colors.css';

.header {
  width: 100%;
  text-align: center;
  background-color: todoBlue;
  position: relative;
}

.title {
  font-size: 2.5rem;
  margin: 0;
  padding: 0.5em;
  color: todoWhite;
}

.logout {
  position: absolute;
  top: 2rem;
  right: 0.5rem;
  padding: 0.8em;
  background-color: todoBlack;
  color: todoWhite;
  outline: 0;
  border: 0;
  border-radius: 10%;
  cursor: pointer;
}

@media screen and (max-width: 1045px) {
  .logout {
    position: absolute;
    top: 1.2rem;
    right: 0.5rem;
    padding: 0.5em;
    background-color: todoBlack;
    color: todoWhite;
    outline: 0;
    border: 0;
    border-radius: 10%;
    cursor: pointer;
  }
}
